import React, { useState } from "react";
import { Link, Outlet } from "react-router-dom";

import logo from '../img/logo.png';

function Navbar() {

    const [nav, SetNav] = useState(false);

    const NavClick = () =>{
        SetNav(!nav);
    }

    return(
        <div className="flex justify-between items-center bg-zinc-950 p-4">

            <div className="pl-2 md:pl-20">
                <Link to="/">
                    <img src={logo} className="h-[60px] w-20" alt="logo"/>
                </Link>
            </div>

            <ul className="flex flex-row justify-end pr-20 gap-5 md:gap-10 max-md:hidden">
                <li className="" onClick={NavClick}>
                    <Link to="/" className="link">Home</Link>
                </li>
                <li className="" onClick={NavClick}>
                    <Link to="/projects" className="link">Projects</Link>
                </li>
                <li className="" onClick={NavClick}>
                    <Link to="/contacts" className="link">Contacts</Link>
                </li>
                <li className="" onClick={NavClick}>
                    <Link to="/pricing" className="link">Create your website</Link>
                </li>
            </ul>

            <ul className={`bg-zinc-950 rounded-3xl absolute text-center gap-5 md:hidden ${nav ? 'top-[110px]' : 'top-[-1500%]'} min-w-[91%] p-5`}>
                <li className="" onClick={NavClick}>
                    <Link to="/" className="link">Home</Link>
                </li>
                <li className="pt-5" onClick={NavClick}>
                    <Link to="/projects" className="link">Projects</Link>
                </li>
                <li className="pt-5 pb-5" onClick={NavClick}>
                    <Link to="/contacts" className="link">Contacts</Link>
                </li>
                <li className="pb-5" onClick={NavClick}>
                    <Link to="/pricing" className="link">Create your website</Link>
                </li>

                <hr className="bg-purple-950 h-[2px]"></hr> 
                
                <div className='logodiscord justify-center pt-3 pb-2'>
                    <svg xmlns="http://www.w3.org/2000/svg" width="30" height="30" fill="#111661" class="bi bi-discord" viewBox="0 0 16 16">
                        <path d="M13.545 2.907a13.2 13.2 0 0 0-3.257-1.011.05.05 0 0 0-.052.025c-.141.25-.297.577-.406.833a12.2 12.2 0 0 0-3.658 0 8 8 0 0 0-.412-.833.05.05 0 0 0-.052-.025c-1.125.194-2.22.534-3.257 1.011a.04.04 0 0 0-.021.018C.356 6.024-.213 9.047.066 12.032q.003.022.021.037a13.3 13.3 0 0 0 3.995 2.02.05.05 0 0 0 .056-.019q.463-.63.818-1.329a.05.05 0 0 0-.01-.059l-.018-.011a9 9 0 0 1-1.248-.595.05.05 0 0 1-.02-.066l.015-.019q.127-.095.248-.195a.05.05 0 0 1 .051-.007c2.619 1.196 5.454 1.196 8.041 0a.05.05 0 0 1 .053.007q.121.1.248.195a.05.05 0 0 1-.004.085 8 8 0 0 1-1.249.594.05.05 0 0 0-.03.03.05.05 0 0 0 .003.041c.24.465.515.909.817 1.329a.05.05 0 0 0 .056.019 13.2 13.2 0 0 0 4.001-2.02.05.05 0 0 0 .021-.037c.334-3.451-.559-6.449-2.366-9.106a.03.03 0 0 0-.02-.019m-8.198 7.307c-.789 0-1.438-.724-1.438-1.612s.637-1.613 1.438-1.613c.807 0 1.45.73 1.438 1.613 0 .888-.637 1.612-1.438 1.612m5.316 0c-.788 0-1.438-.724-1.438-1.612s.637-1.613 1.438-1.613c.807 0 1.451.73 1.438 1.613 0 .888-.631 1.612-1.438 1.612"/>
                    </svg>
                    <h1 className='text-white text-xl'>lori______</h1>
                </div>

            </ul>

            <div className="flex justify-end pr-5 md:hidden">
                <svg xmlns="http://www.w3.org/2000/svg" width="35" height="35" fill="white" className="bi bi-menu-button cursor-pointer" viewBox="0 0 16 16" onClick={NavClick}>
                    <path d="M0 1.5A1.5 1.5 0 0 1 1.5 0h8A1.5 1.5 0 0 1 11 1.5v2A1.5 1.5 0 0 1 9.5 5h-8A1.5 1.5 0 0 1 0 3.5zM1.5 1a.5.5 0 0 0-.5.5v2a.5.5 0 0 0 .5.5h8a.5.5 0 0 0 .5-.5v-2a.5.5 0 0 0-.5-.5z"/>
                    <path d="m7.823 2.823-.396-.396A.25.25 0 0 1 7.604 2h.792a.25.25 0 0 1 .177.427l-.396.396a.25.25 0 0 1-.354 0M0 8a2 2 0 0 1 2-2h12a2 2 0 0 1 2 2v5a2 2 0 0 1-2 2H2a2 2 0 0 1-2-2zm1 3v2a1 1 0 0 0 1 1h12a1 1 0 0 0 1-1v-2zm14-1V8a1 1 0 0 0-1-1H2a1 1 0 0 0-1 1v2zM2 8.5a.5.5 0 0 1 .5-.5h9a.5.5 0 0 1 0 1h-9a.5.5 0 0 1-.5-.5m0 4a.5.5 0 0 1 .5-.5h6a.5.5 0 0 1 0 1h-6a.5.5 0 0 1-.5-.5"/>
                </svg>
            </div>

            <Outlet />

        </div>
    ); 
}

export default Navbar;