import React from 'react';
import { motion } from 'framer-motion';
import '../index.css';


function Projects() { 
  return (
    <motion.section className='home'
      initial={{opacity: 0}}
      animate={{opacity: 1}}
      exit={{opacity: 0}}
      transition={{duration: 2}}
    >
        <h1 className='title md:text-9xl text-5xl mt-[25dvh]'>PROJECTS</h1>
        <div className='flex flex-col mt-[20dvh] mb-[20dvh]'>
        <div className='container-grid'>
            <div className='item'>
                <div className="bg-[url('../src/img/3.png')] hover:animate-pulse bg-no-repeat bg-cover bg-center relative p-20 max-sm:p-10 max-sm:ml-2 rounded-3xl border-2 border-white">
                <h1 className='cardtxt'>COSMO MODELS</h1>
                </div>
            </div>
            <div className='item'>
                <div className="bg-[url('../src/img/1.png')] hover:animate-pulse bg-no-repeat bg-cover bg-center relative p-20 max-sm:p-10 max-sm:ml-2 rounded-3xl border-2 border-white">
                <h1 className='cardtxt'>NEXION <br></br>RP</h1>
                </div>
            </div>
            <div className='item'>
                <div className="bg-[url('../src/img/2.png')] hover:animate-pulse bg-no-repeat bg-cover bg-center relative p-20 max-sm:p-10 max-sm:ml-2 rounded-3xl border-2 border-white">
                <h1 className='cardtxt'>MATT3X <br></br>MODDING</h1>
                </div>
            </div>
        </div>
      </div>
    </motion.section>
  );
}

export default Projects;
