import React from 'react';
import { motion } from 'framer-motion';
import '../index.css';

function Home() { 
  return (
    <motion.section className='home'
      initial={{opacity: 0}}
      animate={{opacity: 1}}
      exit={{opacity: 0}}
      transition={{duration: 2}}
    >
      <div className='absolute top-[50%] left-[50%] translate-x-[-50%] translate-y-[-50%] mt-2'>
        <h1 className='title'>LORI</h1>
        <h1 className='subtitle'>DEVELOPER</h1>
      </div>
    </motion.section>
  );
}

export default Home;
